.topic-header {
  display: flex;
  padding: 18px 19px;
}
.topic-header .header-left {
  width: 63px;
}
.topic-header .header-left .topic-logo-wrapper {
  width: 63px;
  height: 63px;
  will-change: width, height;
  transition-delay: 100ms;
  transition-timing-function: linear;
  transition-duration: 300ms;
  transition-property: width, height;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;
}
.topic-header .header-center {
  flex: 0 1 100%;
  margin: 4px 12px;
}
.topic-header .header-center .header-desc {
  color: #999;
}
.topic-header .header-right {
  width: 32px;
}
.notop .topic-header {
  padding: 12px 19px;
}
.notop .topic-header .header-left .topic-logo-wrapper {
  width: 39px;
  height: 37px;
}
.notop .topic-header .header-center .header-desc {
  display: none;
}
.notop .topic-header .header-right .link.with-share {
  padding: 16px;
}
a.link {
  color: #c0c0c0;
  background-repeat: no-repeat;
  background-position: left 50%;
  background-size: 17px auto;
  padding-left: 25px;
}
a.link.with-facebook {
  background-image: url("/static/images/facebook.png");
}
a.link.with-whatsapp {
  background-image: url("/static/images/WhatsApp share.png");
}
a.link.with-comment {
  background-image: url("/static/images/comments.png");
}
a.link.with-like {
  background-image: url("/static/images/like_0.png");
}
a.link.with-like.clicked {
  color: #ff6060;
  background-image: url("/static/images/like.png");
}
a.link.with-dislike {
  background-image: url("/static/images/dislike.png");
}
a.link.with-dislike.clicked {
  color: #ff6060;
  background-image: url("/static/images/dislike_1.png");
}
a.link.with-extend {
  background-image: url("/static/images/more.png");
}
a.link.with-message {
  background-image: url("/static/images/message.png");
}
a.link.with-share {
  display: inline-block;
  padding: 32px;
  background-size: 32px auto;
  background-image: url("/static/images/share.png");
}
a.link.with-send {
  display: inline-block;
  padding: 16px;
  background-size: 32px auto;
  background-image: url("/static/images/Send.png");
}
a.link.with-big-facebook {
  display: inline-block;
  padding: 50px;
  background-size: 50px auto;
  background-position: 25px 50%;
  background-image: url("/static/images/fackbook-big.png");
}
a.link.with-big-whatsapp {
  display: inline-block;
  padding: 50px;
  background-size: 50px auto;
  background-position: 25px 50%;
  background-image: url("/static/images/whatsapp-big.png");
}
