.message-box-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 200;
  background-color: rgba(0, 0, 0, 0.83);
}
.message-box-wrapper .message-box {
  position: absolute;
  width: 100vw;
  height: auto;
  left: 0;
  bottom: 0;
  background-color: #fff;
}
.message-box-wrapper .message-box .message-area-form {
  position: relative;
  padding: 15px 90px 15px 19px;
}
.message-box-wrapper .message-box .message-area-form > .message-area {
  width: 100%;
}
.message-box-wrapper .message-box .message-area-form > .send-button {
  position: absolute;
  top: 10px;
  right: 19px;
}
