.comment-item-wrapper {
  border-bottom: 1px solid #ccc;
  padding: 16px 19px;
}
.comment-item-wrapper:last-child {
  border-bottom: 0 none;
}
.comment-item-wrapper .comment-item-content {
  padding-bottom: 15px;
}
.comment-item-wrapper .comment-item-extend {
  display: flex;
  justify-content: center;
  font-size: 14px;
}
.comment-item-wrapper .comment-item-extend > .comment-item-extend-piece {
  flex: 1;
  text-align: center;
  position: relative;
}
