.message-item-wrapper {
  border-bottom: 1px solid #ccc;
  padding: 16px 19px;
}
.message-item-wrapper:last-child {
  border-bottom: 0 none;
}
.message-item-wrapper .message-item-content {
  padding-bottom: 15px;
}
.message-item-wrapper .message-item-extend {
  display: flex;
  justify-content: center;
  font-size: 14px;
}
.message-item-wrapper .message-item-extend > .message-item-extend-piece {
  flex: 1;
  text-align: center;
  position: relative;
}
.message-item-wrapper .message-item-extend > .message-item-extend-piece .extend-menu {
  position: absolute;
  top: -10px;
  right: 32px;
  border-radius: 5px;
  box-shadow: 0 0 7px rgba(204, 204, 204, 0.5);
  background-color: #fff;
  padding: 10px;
  pointer-events: none;
  opacity: 0;
  will-change: opacity;
  transition: opacity 300ms ease 0ms;
}
.message-item-wrapper .message-item-extend > .message-item-extend-piece .extend-menu.show {
  opacity: 1;
  pointer-events: auto;
}
.message-item-wrapper .message-item-extend > .message-item-extend-piece .extend-menu .link.with-facebook {
  margin-left: 21px;
  padding-right: 15px;
  border-right: 1px solid #c0c0c0;
}
.message-item-wrapper .message-item-extend > .message-item-extend-piece .extend-menu .link.with-whatsapp {
  margin-left: 21px;
  padding-right: 15px;
}
