main.panel {
  position: relative;
  width: 100vw;
  height: 100vh;
}
main.panel > .panel-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 152px;
  z-index: 100;
  will-change: top, height;
  transition-delay: 100ms;
  transition-timing-function: linear;
  transition-duration: 300ms;
  transition-property: top, height;
  background-color: #fff;
  box-shadow: 0 0 4px #999;
  overflow: hidden;
}
main.panel > .panel-header > .navbar {
  position: relative;
  padding: 0 52px;
  height: 52px;
  line-height: 52px;
  background-color: #075E54;
  color: #fff;
  text-align: center;
}
main.panel > .panel-header > .navbar > .title {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
main.panel > .panel-header > .navbar > .extend {
  position: absolute;
  top: 0;
  height: 52px;
  width: 52px;
}
main.panel > .panel-header > .navbar > .extend.left {
  left: 0;
}
main.panel > .panel-header > .navbar > .extend.right {
  right: 0;
}
main.panel > .panel-body {
  transition: 300ms height linear 0ms;
  margin-top: 152px;
  margin-bottom: 52px;
  transition-delay: 100ms;
  transition-timing-function: linear;
  transition-duration: 300ms;
  transition-property: margin-top, margin-bottom;
  will-change: margin-top, margin-bottom;
}
main.panel > .panel-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100vw;
  height: 52px;
  z-index: 100;
  transition-delay: 0ms;
  transition-timing-function: linear;
  transition-duration: 800ms;
  transition-property: bottom, height;
  will-change: bottom, height;
  background-color: #fff;
  box-shadow: 0 0 4px #999;
}
main.panel > .panel-masker {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 101;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.86);
  opacity: 0;
  pointer-events: none;
  will-change: opacity;
  transition: opacity 300ms ease 0ms;
}
main.panel > .panel-actionbar {
  position: fixed;
  bottom: -101vh;
  left: 0;
  width: 100vw;
  min-height: 100px;
  max-height: 100vh;
  overflow: hidden;
  z-index: 102;
  transition-delay: 0ms;
  transition-timing-function: linear;
  transition-duration: 800ms;
  transition-property: bottom, height;
  will-change: bottom, height;
  background-color: #fff;
  box-shadow: 0 0 4px #999;
}
main.panel > .panel-toast {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 110;
  align-content: center;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 300ms;
  pointer-events: none;
}
main.panel > .panel-toast > .panel-toast-content {
  padding: 12px 20px;
  background-color: rgba(0, 0, 0, 0.57);
  border-radius: 8px;
  color: #fff;
}
main.panel.with-masker > .panel-masker {
  pointer-events: auto;
  opacity: 0.6;
}
main.panel.with-actionbar > .panel-actionbar {
  bottom: 0;
}
main.panel.with-toast > .panel-toast {
  opacity: 1;
  visibility: visible;
}
main.panel.notop > .panel-header {
  height: 108px;
}
main.panel.notop > .panel-body {
  margin-top: 108px;
}
main.panel.noheader > .panel-header {
  top: -100vh;
}
main.panel.noheader > .panel-body {
  margin-top: 0;
}
main.panel.nofooter > .panel-footer {
  bottom: -100vh;
}
main.panel.nofooter > .panel-body {
  margin-bottom: 0;
}
